<script>
/* eslint-disable */

import { formatNum, toDec, fromDec } from "./../decUtil";
import "element-ui/lib/theme-chalk/index.css";
import Proxy from "./../proxy.js";
import { Message, MessageBox } from "element-ui";

export default {
  name: "topMenu",

  props: ['is_home', 'is_guide', 'is_faq'],

  data() {
    return {
      nav: false,
      curr_lan: 'English',
      languagebg: 'enbg',
      home_link: null,
      guide_link: null,
      FAQ_link: null,
      items: [
        { lgName: 'English', lan: 'en_US', bg: 'enbg' },
        { lgName: 'Español', lan: 'sp_US', bg: 'spbg' },
        { lgName: 'français', lan: 'fr_US', bg: 'frbg' },
        { lgName: 'Deutsche', lan: 'ge_US', bg: 'gebg' },
        { lgName: 'italiano', lan: 'it_US', bg: 'itbg' },
        { lgName: 'русский',lan: 'ru_US', bg: 'rubg' },
        { lgName: 'Português', lan: 'po_US', bg: 'pobg' },
        { lgName: 'عربى', lan: 'ar_US', bg: 'arbg' },
        { lgName: '日本語', lan: 'ja_US', bg: 'jabg' },
        { lgName: '한국어', lan: 'ko_US', bg: 'kobg' },
        { lgName: '中文', lan: 'zh_US', bg: 'zhbg' }
      ]
    };
  },

  created() {
    this.getInvite();
    let lan = localStorage.getItem('langs');
    for( var i=0; i < this.items.length; i++) {
        if ( this.items[i]["lan"] === lan ) {
          this.curr_lan = this.items[i]["lgName"];
          this.languagebg = this.items[i]["bg"];
        }
      }
  },

  methods: {

    getInvite() {
      let addr = this.$route.params.p;
      if (addr && addr.length === 42) {
        this.parentAddr = addr;
        this.home_link = "#/" + this.parentAddr;
        this.guide_link = "#/guide/"+ this.parentAddr;
        this.FAQ_link = "#/faq/"+ this.parentAddr;
      } else {
        this.guide_link = "#/guide";
        this.home_link = "#/";
        this.FAQ_link = "#/faq";
      }
    },

    langToggle(lan) {
      this.$i18n.locale = lan;
      localStorage.setItem("langs", lan);
      for( var i=0; i < this.items.length; i++) {
        if ( this.items[i]["lan"] === lan) {
          this.curr_lan = this.items[i]["lgName"];
          this.languagebg = this.items[i]["bg"];
        }
      }
    },

    langToggleMB(e) {
      this.$i18n.locale = e.target.value;
      localStorage.setItem("langs", e.target.value);
    },

    navClick() {
      this.nav = !this.nav;
    },

    hideNavClick() {
      this.nav = !this.nav;
    },
  },
};
</script>


