import Vue from 'vue'
import Router from 'vue-router'
import EthLife from './components/EthLife.vue'
import Guide from './components/guide.vue'
import FAQ from './components/faqInterstellars.vue'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'main',
      component: EthLife
    },
    {
      path: '/guide',
      name: 'guide',
      component:   Guide
    },
    {
      path: '/guide/:p',
      name: 'guide',
      component:   Guide
    },
    {
      path: '/faq',
      name: 'faq',
      component:   FAQ
    },
    {
      path: '/faq/:p',
      name: 'faq',
      component:   FAQ
    },
    {
      path: '/:p',
      name: 'main',
      component: EthLife
    },

  ]
})